<template>
  <b-container mt-5 class="text-center">


      <h1>{{modelo}}s</h1> 

      <b-alert
  :show="dismissCountDown"
  dismissible
  :variant="mensaje.color"
  @dismissed="dismissCountDown=0"
  @dismiss-count-down="countDownChanged"
>
  {{mensaje.texto}}
</b-alert>

 <b-modal id="modal_cargando" hide-footer no-close-on-backdrop hide-header centered="">
 <h4 class="text-center">Cargando...</h4>
</b-modal>




  <b-button v-b-modal.modal_guardar variant="primary" @click="seleccionModalPrincipal('modal_guardar','Guardar')">Nuevo {{modelo}}</b-button>

  <b-modal :id="modal_principal" hide-footer no-close-on-backdrop hide-header centered="">

<form @submit.prevent="seleccionGuardar(dato)">
  <h3 class="text-center">{{titulo_modal_principal}}</h3>
  <h5 hidden>{{idDato}}</h5>
  <input type="text" placeholder="descripcion" class="form-control my-2" v-model="dato.descripcion">
  <input type="text" placeholder="codigo" class="form-control my-2" v-model="dato.codigo">
  <input type="number" placeholder="cantidad" class="form-control my-2" v-model="dato.cantidad">
  <input type="number" placeholder="precio" class="form-control my-2" v-model="dato.precio">
  <input type="number" placeholder="costo" class="form-control my-2" v-model="dato.costo">
  <b-button class="form-control"  variant="primary" type="submit" @click="$bvModal.hide(modal_principal)">Guardar</b-button>
  <b-button class="form-control mt-2" @click="$bvModal.hide(modal_principal)">Volver</b-button>
  </form>

  </b-modal>



 <table class="table table-bordered table-hover table-responsive-sm text-center mt-4">

 <thead class="thead-dark">
      <tr>
      <th v-for="(item, index) of columnas_tabla" :key="index" scope="col">{{item}}</th>
     </tr>
</thead>

<tbody class="table-body" v-for="(item, index) of datos" :key="index">
    
    <tr>
      
        <td>{{item.descripcion}}</td>
        <td>{{item.codigo}}</td>
        <td>{{item.cantidad}}</td>
        <td>{{item.precio}}</td>
        <td>{{item.costo}}</td>

        <td>
           <b-button variant="success" v-b-modal.modal_editar @click="itemSeleccionado('modal_editar','Editar', item._id, item.descripcion, item.codigo, item.cantidad, item.precio, item.costo)">
              <span class="h5 mb-2"><b-icon icon="pencil"></b-icon></span>
           </b-button>

             <b-button class="m-2" v-b-modal.modal_eliminar variant="danger" @click="idSeleccionado(item._id)">
                <span class="h5 mb-2"><b-icon icon="trash"></b-icon></span>
             </b-button>
        </td>

    </tr>
</tbody>
 

</table>



  <b-modal id="modal_eliminar" hide-footer no-close-on-backdrop hide-header size="sm" centered="">

<h4 class="text-center">Eliminar {{modelo}}</h4>
<b-button variant="danger" class="form-control mt-2" @click="eliminar">Eliminar</b-button>
<b-button class="form-control mt-2" @click="$bvModal.hide('modal_eliminar')">Volver</b-button>

  </b-modal>


  </b-container>
</template>

<script>
export default {
name:'Clientes',


data(){
    return {
      modelo: 'producto',
      columnas_tabla :['descripcion','codigo','cantidad','precio','costo','operaciones'],
datos:[],
dato:{},
agregar: true,
      mensaje: {color: '', texto: ''},
      dismissSecs: 5,
      dismissCountDown: 0,
      idDato : 0,
      titulo_modal_principal : '',
      modal_principal : ''
    }
},
created(){
this.ver_todos();
}, 

methods:{

// UTIL ************************************************** 
valoresDato(descripcion, codigo, cantidad, precio, costo){
this.dato.descripcion = descripcion;
this.dato.codigo =codigo;
this.dato.cantidad = cantidad;
this.dato.precio = precio;
this.dato.costo = costo;
    },

limpiarValores(){
this.valoresDato('','','','','')
},

seleccionModalPrincipal(modal, titulo){
  this.limpiarValores()
this.modal_principal = modal;
this.titulo_modal_principal = titulo + ' ' + this.modelo;
},
    alerta(){
        this.showAlert('success', 'Prueba de mensaje...')
    },
           countDownChanged(dismissCountDown) {
      this.dismissCountDown = dismissCountDown;
    },

    showAlert(color, texto) {
                this.mensaje.color= color;
        this.mensaje.texto = texto;
      this.dismissCountDown = this.dismissSecs;
    },
    
    showCargando(){
      this.$bvModal.show('modal_cargando');
    },
    hideCargando(){
  this.$bvModal.hide('modal_cargando');
    },
        
    ocultarModal(id_modal) {
       this.$bvModal.hide(id_modal);
      },
    
    idSeleccionado(item){
      this.idDato = item;
    },

    itemSeleccionado(modal, titulo_modal, id, descripcion, codigo, cantidad, precio, costo){
this.seleccionModalPrincipal(modal,titulo_modal)
 this.idDato = id;
this.valoresDato(descripcion, codigo, cantidad, precio, costo)
    },

    seleccionGuardar(dato){
if(this.modal_principal === 'modal_guardar'){
  this.guardar(dato)
}else{
  this.editar(dato)
}
    },


//CRUD **********************************

    ver_todos(){
        this.axios.get(`/${this.modelo}`)
        .then(res =>{
            const datos = res.data;
           this.datos = datos;
        })

        .catch(e=>{
            console.log(e)
        })
    },

    guardar(item){

         let config = {
  onUploadProgress: progressEvent => {
 this.showCargando()
  }
}

        this.axios.post(`/${this.modelo}/guardar`, item, config)
        .then(res=>{

            this.showAlert('success',res.data)

         this.ver_todos()
         
       this.hideCargando()

        })

        .catch(err=>{
              this.showAlert('danger','Error: ' + err);
        })
    },

    eliminar(){

      const url_api = `/${this.modelo}/eliminar/${this.idDato}`;

      this.axios.get(url_api)
      .then(res=>{

      this.showAlert('success', res.data);
      this.ver_todos();
      
      this.ocultarModal('modal_eliminar')

      this.hideCargando()

      })

      .catch(err=>{
this.showAlert('danger',err);
      })


    },

       editar(item){

         let config = {
  onUploadProgress: progressEvent => {
 this.showCargando()
  }
}
   
      const url_api = `/${this.modelo}/editar/${this.idDato}`;

      this.axios.post(url_api, item, config)
      .then(res=>{

      this.showAlert('success', res.data);
      this.ver_todos();
      
      this.ocultarModal('modal_editar')

      this.hideCargando()


      })

      .catch(err=>{
this.showAlert('danger',err.data);
console.log(err.data)
      })



    }

}


}
</script>
